import React from 'react';
import PropTypes from 'prop-types';

import { FixedLayout, Div, Panel, PanelHeader, Group, Cell, Avatar, IOS, platform } from '@vkontakte/vkui';

import './Hello.css';

const Update = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Политика</PanelHeader>
		{fetchedUser &&
		<Group>
			<FixedLayout vertical="top">
			
			<Div className="Hello">
				{fetchedUser.photo_200 && <Avatar src={fetchedUser.photo_200}/>}
	<h2>Привет,{fetchedUser.first_name}!</h2>
	<h3>На данной странице опубликована важная информация. Прочитай её!</h3>
            </Div>
			</FixedLayout>
		</Group>}

		
	</Panel>
);

Update.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Update;