
import React from 'react';

import { FixedLayout, Div, Button, FormStatus, FormLayout, Gallery, Separator, Tabbar, TabbarItem, Placeholder, Avatar, Counter, Card, CardGrid, platform, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


import angel from '../img/angel.png';
import bolnoy from '../img/bolnoy.png';
import dead from '../img/dead.png';
import update from '../img/update.png';
import './Covid.css';
import './Update.css';
//иконки
import Icon28AirplayVideoOutline from '@vkontakte/icons/dist/28/airplay_video_outline'; //обучение
import Icon28LocationOutline from '@vkontakte/icons/dist/28/location_outline'; //такси
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; //covid
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'; //новости
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline'; //организации
import Icon28MenuOutline from '@vkontakte/icons/dist/28/menu_outline'; //ещё


const Covid = props => (
    <Panel id={props.id}>

        <PanelHeader
        >
         COVID-19
        </PanelHeader>
<Group>

        <Gallery
     slideWidth="100%"
     align="center"
     style={{ height: 240 }}
   >
       <Card size="l">
 <Placeholder
 icon={<img className="Covid" src={angel}/>}
 header="Выздоровело: 36"
>
 Количество выписанных
 <br/>
 за
 <br/>
 весь период.
</Placeholder>
<div style={{ width: 96, height: 170 }} />
</Card>
     <Card size="l">
<Placeholder
 icon={<img className="Covid" src={bolnoy}/>} 
 header="Заражено: 54"
>
 Количество людей,
 <br/>
 которые
 <br/>
 находятся на лечении.
</Placeholder>
<div style={{ width: 96, height: 170 }} />
</Card>
     <Card size="l">
<Placeholder
 icon={<img className="Covid" src={dead}/>}
 header="Умерло: 0"
>
 Количество умерших
 <br/>
 людей за
  <br/>
  весь период.
</Placeholder>
<div style={{ width: 96, height: 170 }} />
</Card>
   </Gallery>


   <br/>
   <FormLayout>
       <FormStatus header="Подсказка" mode="warning">
       Чтобы узнать больше информации, сделайте свайп по табличке в сторону.
       </FormStatus>
   </FormLayout>

        

    </Group>
    <br/>
    <Div>
       <Button size="xl" mode="outline" onClick={props.go} data-to="covidworld">Мировая статистика</Button>
     </Div>
    <br/>
    <FixedLayout vertical="bottom">
<Tabbar>
              <Separator wide />
                      <TabbarItem text="Организ." onClick={props.go} data-to="organizations"><Icon28HomeOutline />
                      </TabbarItem>
                      <TabbarItem text="COVID" onClick={props.go} data-to="covid"><Icon28BugOutline fill="#FF0000" />
                      </TabbarItem>
                      <TabbarItem text="Новости" onClick={props.go} data-to="home"><Icon28NewsfeedOutline />
                      </TabbarItem>
                      <TabbarItem text="Образование" onClick={props.go} data-to="education"><Icon28AirplayVideoOutline />
                      </TabbarItem>
                      <TabbarItem text="Такси" onClick={props.go} data-to="taxi"><Icon28LocationOutline />
                      </TabbarItem>
                      <TabbarItem text="Ещё" onClick={props.go} data-to="still"><Icon28MenuOutline />
                      </TabbarItem>
          </Tabbar>
          </FixedLayout>
    </Panel>
);

export default Covid;